import Menu from './Menu'
import Vue from 'vue'

export default class MenuManager {

}

MenuManager.refreshMenus = function (user) {
  let menus = []

 /* //
  let menuRecent = new Menu('', '/user/profile', false, 'fa fa-clock-o')
  menus.push(menuRecent)*/

  //
  let menuAll = new Menu('', '/matter/list/root', false, 'fa fa-th')
  menus.push(menuAll)

  //
  let menuWord = new Menu('', '/matter/word/list',false,'fa fa-file-text-o')
  menus.push(menuWord)

  //
  let menuPicture = new Menu('', '/matter/photo/list', false, 'fa fa-picture-o')
  menus.push(menuPicture)

  //
  let menuVideo = new Menu('', '/matter/video/list',false,'fa fa-film')
  menus.push(menuVideo)

  //
  let menuMusic = new Menu('', '/matter/music/list',false,'fa fa-music')
  menus.push(menuMusic)

  //
  if(user.role === 'ADMINISTRATOR'){
    let menuUser = new Menu('', '/user/list',false,'fa fa-user')
    menus.push(menuUser)
  }


  return menus
}


